import './Navbar.css'
import {Link} from "react-router-dom";

const Navbar = () => {
    return (
        <nav className='navbar'>
            <Link className='navbar__menu_item' to={'/#'}></Link>
            <Link className='navbar__menu_item' to={'/profile'}></Link>
            <Link className='navbar__menu_item' to={'/users'}></Link>
        </nav>
    )
}

export default Navbar;